/**
 * @callback fnPlay
 * @param {number} time
 */
/**
 * @callback fnState
 * @instance playlist
 * @return {null|string}
 */
/**
 * @callback fnIsLoaded
 * @instance playlist
 * @return {boolean}
 */

/**
 * @typedef playlist
 *
 * @property {function} init
 * @property {string} name
 * @property {Howl} stream
 * @property {fnPlay} play
 * @property {function} pause
 * @property {function} getTime
 * @property {fnState} state
 * @property {fnIsLoaded} isLoaded
 */

/**
 *
 * @param {string} name
 * @param {number} totalChunks
 * @param {function} onend
 * @return {playlist[]}
 */
export const createPlaylist = (name, totalChunks = 3, onload, onend) => {
  const streams = Array(totalChunks);

  for (let i = 1; i <= totalChunks; i += 1) {
    streams[i] = {
      name: `${name}.act${i}`,
      isValid() {
        if (streams[i].stream) {
          return true;
        }
        console.error(`invalid stream ${this.name}`);
        return false;
      },
      /**
       *
       * @param {number} time
       */
      play(time) {
        if (!this.isValid()) { return; }

        if (time) {
          this.stream.seek(time);
        }

        console.log(`${this.name} play at ${time || 0}s`);

        this.stream.play();
      },
      pause() {
        if (!this.isValid() || !this.stream.playing()) { return; }
        console.log(`${this.name} pause`);
        this.stream.pause();
      },
      getTime() {
        if (!this.isValid()) { return null; }
        return this.stream.seek();
      },
      duration() {
        if (!this.isValid()) { return 0; }
        return this.stream.duration();
      },
      state() {
        if (!this.isValid()) { return null; }
        const state = this.stream.state();
        console.log(`${this.name} state: ${state}`);
        return state;
      },
      isLoaded() {
        return this.state() === 'loaded';
      }
    };
    streams[i].init = function (onSuccess) {
      if (streams[i].stream === undefined) {
        streams[i].stream = new howler.Howl({
          src: __USE_SMALL__
            ? [
              `assets/audio/${name}.act${i}.mp3`
            ]
            : [
              `assets/audio/${name}.act${i}.webm`,
              `assets/audio/${name}.act${i}.mp3`
            ],
          autoUnlock: true,
          preload: true,
          html5: true, // needed for streaming
          html5PoolSize: 20,
          onend,
          onload: () => {
            console.log(`${streams[i].name} loaded`);
            onload(i);
          },
          onloaderror: (e) => {
            console.error(name);
            console.error(e);
          },
          onplayerror: (e) => {
            console.error(name);
            console.error(e);
          }
        });
        onSuccess();
      }
    };
  }

  return streams;
};
